@use 'base/variables' as v;
@use 'base/mixins' as m;

.result {
    position: relative;
    width: 100%;
    height: 6.5rem;
    overflow: hidden;

    &__info {
        position: absolute;
        bottom: .4rem;
        color: v.$primario;
        font-size: .8rem;
        transition: all 150ms ease-in-out;
        transform: translateY(200%);
        opacity: 0;  
        font-size: 1.2rem;      
    }

    &__info.rigth {
        right: .8rem;
    }

    &__info.left {
        left: .8rem;
    }

    &__viewbox {
        width: 100%;
        height: 100%;
        background: rgba(254, 111, 16, 0.089);
        border-radius: .8rem;
        color: v.$secundario;
        text-align: center;
        line-height: 6.5rem;
                
    }
    & #copy-btn {
        position: absolute;
        top: var(--y);
        left: var(--x);
        width: 3.8rem;
        height: 3.8rem;
        background: v.$gris;
        border-radius: 50%;
        opacity: 0;
        transform: translate(-50%, -50%) scale(0);
        transition: all 350ms cubic-bezier(0.175, 0.885, 0.32, 1.275);
        cursor: pointer;
        z-index: 2;
    
        &:active {
            box-shadow: 0 0 0 50rem  rgba(254, 111, 16, 0.1);
        }        
        
    }

    &:hover #copy-btn {
        opacity: 1;
        transform: translate(-50%, -50%) scale(1);
    }

}

.field-title {
    position: absolute;
    top: -1rem;
    left: .8rem;
    transform: translateY(-50%);
    font-weight: 700;
    color: v.$secundario;
    text-transform: uppercase;
    font-size: 1.6rem;
    pointer-events: none;
    user-select: none;
}