@use 'base/variables' as v;
@use 'base/mixins' as m;

.footer {    
    font-weight: 400;
    text-align: center;
    margin: 1rem;
    text-transform: uppercase;
    
    
    p {
        color: v.$secundario; 
        font-size: 1.3rem;
        @include m.tablet {
            font-size: 1.6rem;
        }          
    }

    a {
        color: v.$primario;        
        margin: 1rem;
        font-size: 1.5rem;
        @include m.tablet {
            font-size: 1.8rem;
        }  
        
        &:hover {
            text-decoration: underline;
        }
    }

    
}