.options {
  width: 100%;
  height: auto;
  margin: 5rem 0;
}

.support {
  position: fixed;
  right: 1rem;
  bottom: 1rem;
  padding: 1rem;
  display: flex;
}

/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  width: 100%;
  height: 100vh;
  font-size: 1.6rem;
  background-color: #e3e3e3;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  font-family: "Poppins", sans-serif;
}

a {
  margin: 0 2rem;
  color: #707070;
  font-size: 1.6rem;
  transition: all 400ms ease;
  text-decoration: none;
}
a:hover {
  color: #e3e3e3;
}

img {
  max-width: 100%;
  display: block;
}

.logo {
  margin: 1rem 0;
}

.container {
  width: 30rem;
  height: 52rem;
  padding: 1rem 2.5rem;
  background-color: #FFFFFF;
  border-radius: 1rem;
  box-shadow: 0 0.5rem 1.5rem 0 rgba(112, 112, 112, 0.54);
  font-family: "Poppins", sans-serif;
}
@media (min-width: 768px) {
  .container {
    width: 40rem;
  }
}

.result {
  position: relative;
  width: 100%;
  height: 6.5rem;
  overflow: hidden;
}
.result__info {
  position: absolute;
  bottom: 0.4rem;
  color: #fe6f10;
  font-size: 0.8rem;
  transition: all 150ms ease-in-out;
  transform: translateY(200%);
  opacity: 0;
  font-size: 1.2rem;
}
.result__info.rigth {
  right: 0.8rem;
}
.result__info.left {
  left: 0.8rem;
}
.result__viewbox {
  width: 100%;
  height: 100%;
  background: rgba(254, 111, 16, 0.089);
  border-radius: 0.8rem;
  color: #707070;
  text-align: center;
  line-height: 6.5rem;
}
.result #copy-btn {
  position: absolute;
  top: var(--y);
  left: var(--x);
  width: 3.8rem;
  height: 3.8rem;
  background: #e3e3e3;
  border-radius: 50%;
  opacity: 0;
  transform: translate(-50%, -50%) scale(0);
  transition: all 350ms cubic-bezier(0.175, 0.885, 0.32, 1.275);
  cursor: pointer;
  z-index: 2;
}
.result #copy-btn:active {
  box-shadow: 0 0 0 50rem rgba(254, 111, 16, 0.1);
}
.result:hover #copy-btn {
  opacity: 1;
  transform: translate(-50%, -50%) scale(1);
}

.field-title {
  position: absolute;
  top: -1rem;
  left: 0.8rem;
  transform: translateY(-50%);
  font-weight: 700;
  color: #707070;
  text-transform: uppercase;
  font-size: 1.6rem;
  pointer-events: none;
  user-select: none;
}

.range__slider {
  position: relative;
  width: 100%;
  height: 5.5rem;
  display: flex;
  justify-content: center;
  align-items: center;
  background: rgba(254, 111, 16, 0.089);
  border-radius: 0.8rem;
  margin: 3rem 0;
}
.range__slider::before, .range__slider::after {
  position: absolute;
  color: #fe6f10;
  font-size: 1.5rem;
  font-weight: bold;
}
.range__slider::before {
  content: attr(data-max);
  left: 1rem;
}
.range__slider::after {
  content: attr(adata-max);
  right: 1rem;
}
.range__slider .length__title::after {
  content: attr(data-length);
  position: absolute;
  right: -2rem;
  font-variant-numeric: tabular-nums;
  color: #fe6f10;
}

#slider {
  -webkit-appearance: none;
  width: calc(100% - 7rem);
  height: 0.3rem;
  border-radius: 0.8rem;
  background: rgba(255, 255, 255, 0.314);
  outline: none;
  padding: 0;
  margin: 0;
  cursor: pointer;
}
#slider::-webkit-slider-thumb {
  -webkit-appearance: none;
  width: 2rem;
  height: 2rem;
  border-radius: 50%;
  background: #fe6f10;
  cursor: pointer;
  transition: all 0.15s ease-in-out;
}
#slider::-webkit-slider-thumb:hover {
  background: #e3e3e3;
  transform: scale(1.2);
}
#slider::-moz-range-thumb {
  width: 2rem;
  height: 2rem;
  border: 0;
  border-radius: 50%;
  background: #fe6f10;
  cursor: pointer;
  transition: background 0.15s ease-in-out;
}
#slider::-moz-range-thumb:hover {
  background: #e3e3e3;
}

.settings {
  position: relative;
  height: auto;
  widows: 100%;
  display: flex;
  flex-direction: column;
}
.settings .setting {
  position: relative;
  width: 100%;
  height: 5.5rem;
  background: rgba(254, 111, 16, 0.089);
  border-radius: 0.8rem;
  display: flex;
  align-items: center;
  padding: 1rem 2.5rem;
  color: #707070;
  margin-bottom: 0.8rem;
}
.settings .setting input {
  opacity: 0;
  position: absolute;
}
.settings .setting input + label {
  user-select: none;
}
.settings .setting input + label::before, .settings .setting input + label::after {
  content: "";
  position: absolute;
  transition: 150ms cubic-bezier(0.24, 0, 0.5, 1);
  transform: translateY(-50%);
  top: 50%;
  right: 1rem;
  cursor: pointer;
}
.settings .setting input + label::before {
  height: 3rem;
  width: 5rem;
  border-radius: 3rem;
  background: #e3e3e3;
}
.settings .setting input + label::after {
  height: 2.4rem;
  width: 2.4rem;
  border-radius: 6rem;
  right: 3.2rem;
  background: #707070;
}
.settings .setting input:checked + label:before {
  background: rgba(254, 111, 16, 0.2);
  transition: all 150ms cubic-bezier(0, 0, 0, 0.1);
}
.settings .setting input:checked + label:after {
  right: 1.4rem;
  background: #fe6f10;
}

.settings .setting input:focus + label::before {
  box-shadow: 0 0 0 0.2rem rgba(255, 255, 255, 0.75);
}
.settings .setting input:disabled + label::before, .settings .setting input:disabled + label::after {
  cursor: not-allowed;
}
.settings .setting input:disabled + label::before {
  background: #707070;
}
.settings .setting input:disabled + label::after {
  background: #e3e3e3;
}

.btn.generate {
  user-select: none;
  position: relative;
  background-color: #fe6f10;
  font-size: 1.6rem;
  font-weight: 700;
  text-transform: uppercase;
  border-radius: 0.8rem;
  display: block;
  height: 5rem;
  margin: 1rem 0;
  border: none;
  color: #FFFFFF;
  transition: all 150ms ease;
  width: 100%;
}
.btn.generate:hover {
  cursor: pointer;
  background-color: #da5801;
}
.btn.generate:active {
  transform: translateY(-3%);
  box-shadow: 0 0.4rem 0.8rem rgba(255, 255, 255, 0.08);
}
@media (min-width: 768px) {
  .btn.generate {
    width: 100%;
    display: inline-block;
  }
}

.footer {
  font-weight: 400;
  text-align: center;
  margin: 1rem;
  text-transform: uppercase;
}
.footer p {
  color: #707070;
  font-size: 1.3rem;
}
@media (min-width: 768px) {
  .footer p {
    font-size: 1.6rem;
  }
}
.footer a {
  color: #fe6f10;
  margin: 1rem;
  font-size: 1.5rem;
}
@media (min-width: 768px) {
  .footer a {
    font-size: 1.8rem;
  }
}
.footer a:hover {
  text-decoration: underline;
}