@use 'base/variables' as v;
@use 'base/mixins' as m;

.settings {
    position: relative;
    height: auto;
    widows: 100%;
    display: flex;
    flex-direction: column;

    .setting {
        position: relative;
        width: 100%;
        height: calc(6.5rem - 1rem);
        background: rgba(254, 111, 16, 0.089);
        border-radius: .8rem;
        display: flex;
        align-items: center;
        padding: 1rem 2.5rem;
        color: v.$secundario;
        margin-bottom: .8rem;

        input {
            opacity: 0;
            position: absolute;

            & + label {
                user-select: none;

                &::before,
                &::after {
                    content: "";
                    position: absolute;
                    transition: 150ms cubic-bezier(0.24, 0, 0.5, 1);
                    transform: translateY(-50%);
                    top: 50%;
                    right: 1rem;
                    cursor: pointer;
                }

                &::before {
                    height: 3rem;
                    width: 5rem;
                    border-radius: 3rem;
                    background: v.$gris ;
                }

                &::after {
                    height: 2.4rem;
                    width: 2.4rem;
                    border-radius: 6rem;
                    right: 3.2rem;
                    background: v.$secundario;
                }
            }
        }
        input:checked + label:before {
            background: rgba(254, 111, 16, 0.2) ;
            transition: all 150ms cubic-bezier(0, 0, 0, 0.1);
        }
        input:checked + label:after {
            right: 1.4rem;
            background: v.$primario;
        }
    }

}

.settings .setting input {
        
    &:focus + label::before{
        box-shadow: 0 0 0 .2rem rgba(255,255,255, 0.75);        
    }
    
    &:disabled + label::before,
    &:disabled + label::after {
        cursor: not-allowed;
    }
    &:disabled + label::before {
        background: v.$secundario;
    }
    &:disabled + label::after {
        background: v.$gris;
    }
}



