@use 'variables' as v; 

/** Media Queries **/
@mixin telefono {
    @media (min-width: v.$telefono) {
        @content;
    }
}

@mixin tablet {
    @media (min-width: v.$tablet) {
        @content;
    }
}
  
@mixin desktop {
    @media (min-width: v.$desktop) {
        @content;
    }
}


@mixin boton($color, $texto) {
    user-select: none;
    position: relative;
    background-color: $color;      
    font-size: 1.6rem;   
    font-weight: 700;
    text-transform: uppercase;
    border-radius: .8rem;    
    display: block;    
    height: 5rem;
    margin: 1rem 0;
    border: none;    
    color: $texto;    
    transition: all 150ms ease;  
    width: 100%;   

    &:hover {
        cursor: pointer;
        background-color: darken($color, 10);
    }
    &:active {
        transform: translateY(-3%);
        box-shadow: 0 .4rem .8rem rgba(255,255,255, 0.08);
    }

    @include tablet {
        width: 100%;
        display: inline-block;
        
    }
}


