@use 'base/variables' as v;
@use 'base/mixins' as m;

.container {  
    width: 30rem;     
    height: 52rem;
    padding: 1rem 2.5rem;
    background-color: v.$blanco;
    border-radius: 1rem;
    box-shadow: 0 .5rem 1.5rem 0 rgba(112,112,112,0.54);
    font-family: v.$fuente_principal;    

    @include m.tablet {
        width: 40rem;
    }  
    
 }
 