@use 'base/variables' as v;
@use 'base/mixins' as m;

.range__slider {
    position: relative;
    width: 100%;
    height: calc(6.5rem - 1rem);
    display: flex;
    justify-content: center;
    align-items: center;
    background: rgba(254, 111, 16, 0.089);
    border-radius: .8rem;
    margin: 3rem 0;

    &::before, &::after {
        position: absolute;
        color: v.$primario;
        font-size: 1.5rem;
        font-weight: bold;
        
    }
    &::before {
        content: attr(data-max);
        left: 1rem;
    }
    &::after {
        content: attr(adata-max) ;
        right: 1rem;
    }

    & .length__title::after {
        content: attr(data-length);
        position: absolute;
        right: -2rem;
        font-variant-numeric: tabular-nums;
        color: v.$primario;
    }
}

#slider {
    -webkit-appearance: none;
    width: calc(100% - (7rem));
    height: .3rem;
    border-radius: .8rem;
    background: rgba(255,255,255, 0.314);
    outline: none;
    padding: 0;
    margin: 0;
    cursor: pointer;

    &::-webkit-slider-thumb {
        -webkit-appearance: none;
        width: 2rem;
        height: 2rem;
        border-radius: 50%;
        background: v.$primario;
        cursor: pointer;
        transition: all 0.15s ease-in-out;

        &:hover {
            background: v.$gris;
            transform: scale(1.2);
        }
    }

    &::-moz-range-thumb {
        width: 2rem;
        height: 2rem;
        border: 0;
        border-radius: 50%;
        background: v.$primario;
        cursor: pointer;
        transition: background 0.15s ease-in-out ;

        &:hover {
            background: v.$gris;
        }
    }
}