@use 'variables' as v; 
@use 'mixins' as m; 

html {
    font-size: 62.5%;
    box-sizing: border-box;
}
*, *:before, *:after {
    box-sizing: inherit;
}
  
body {
    width: 100%;
    height: 100vh;
    font-size: 1.6rem;
    background-color: v.$gris;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    font-family: v.$fuente_principal;   
}


a {
    margin: 0 2rem;
    color: v.$secundario;
    font-size: 1.6rem;
    transition: all 400ms ease;
    text-decoration: none;
    &:hover {
        color: v.$gris;
    }
}

img {
    max-width: 100%;
    display: block;
}

